import { Component, OnInit, Input } from '@angular/core';

@Component({
  selector: 'app-session-list-minimal-skeleton',
  templateUrl: './session-list-minimal-skeleton.component.html',
  styleUrls: ['./session-list-minimal-skeleton.component.scss'],
})
export class SessionListMinimalSkeletonComponent implements OnInit {
  @Input() times:number;
  numbers;
  constructor() { }

  ngOnInit() {
    this.numbers = Array(this.times).fill(0).map((x,i)=>i);
  }

}
